'use client'

import { useState, useRef, useEffect } from 'react'
import NavBar from '@/components/NavBar'
import Footer from '@/components/Footer'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'

interface Message {
  id: number
  text: string
  sender: 'user' | 'support'
  timestamp: Date
}

export default function ChatPage() {
  const [messages, setMessages] = useState<Message[]>([
    {
      id: 1,
      text: "Hello! Welcome to Wrench Go support. How can we help you today?",
      sender: 'support',
      timestamp: new Date()
    }
  ])
  const [newMessage, setNewMessage] = useState('')
  const [isTyping, setIsTyping] = useState(false)
  const messagesEndRef = useRef<HTMLDivElement>(null)

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' })
  }

  useEffect(() => {
    scrollToBottom()
  }, [messages])

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!newMessage.trim()) {
      return
    }

    const userMessage: Message = {
      id: Date.now(),
      text: newMessage,
      sender: 'user',
      timestamp: new Date()
    }

    setMessages(prev => [...prev, userMessage])
    setNewMessage('')
    setIsTyping(true)

    // Simulate support response
    setTimeout(() => {
      const supportResponses = [
        "Thank you for your message. A support representative will be with you shortly.",
        "I understand your concern. Let me help you with that. Can you provide more details about your vehicle?",
        "That's a great question! Our mobile mechanics can definitely help with that service.",
        "I'd be happy to help you schedule a service. What type of vehicle do you have and what's the issue?",
        "For immediate assistance, you can also call us at 819 319 9799. Otherwise, I'm here to help!"
      ]

      const randomResponse = supportResponses[Math.floor(Math.random() * supportResponses.length)]
      
      const supportMessage: Message = {
        id: Date.now() + 1,
        text: randomResponse,
        sender: 'support',
        timestamp: new Date()
      }

      setMessages(prev => [...prev, supportMessage])
      setIsTyping(false)
    }, 1500)
  }

  const formatTime = (date: Date) => {
    return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })
  }

  return (
    <div className="min-h-screen flex flex-col">
      <NavBar />
      
      <main className="flex-1 bg-gray-50">
        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
          <div className="bg-white rounded-lg shadow-lg overflow-hidden">
            {/* Chat Header */}
            <div className="bg-blue-600 text-white p-4">
              <div className="flex items-center space-x-3">
                <div className="bg-blue-500 rounded-full w-10 h-10 flex items-center justify-center font-bold">
                  WG
                </div>
                <div>
                  <h1 className="text-lg font-semibold">Wrench Go Support</h1>
                  <p className="text-blue-200 text-sm">We're here to help with your automotive needs</p>
                </div>
              </div>
            </div>

            {/* Messages Container */}
            <div className="h-96 overflow-y-auto p-4 space-y-4">
              {messages.map((message) => (
                <div
                  key={message.id}
                  className={`flex ${message.sender === 'user' ? 'justify-end' : 'justify-start'}`}
                >
                  <div
                    className={`max-w-xs lg:max-w-md px-4 py-2 rounded-lg ${
                      message.sender === 'user'
                        ? 'bg-blue-600 text-white'
                        : 'bg-gray-200 text-gray-800'
                    }`}
                  >
                    <p className="text-sm">{message.text}</p>
                    <p
                      className={`text-xs mt-1 ${
                        message.sender === 'user' ? 'text-blue-200' : 'text-gray-500'
                      }`}
                    >
                      {formatTime(message.timestamp)}
                    </p>
                  </div>
                </div>
              ))}

              {isTyping && (
                <div className="flex justify-start">
                  <div className="bg-gray-200 text-gray-800 px-4 py-2 rounded-lg">
                    <div className="flex space-x-1">
                      <div className="w-2 h-2 bg-gray-500 rounded-full animate-bounce"></div>
                      <div className="w-2 h-2 bg-gray-500 rounded-full animate-bounce" style={{ animationDelay: '0.1s' }}></div>
                      <div className="w-2 h-2 bg-gray-500 rounded-full animate-bounce" style={{ animationDelay: '0.2s' }}></div>
                    </div>
                  </div>
                </div>
              )}

              <div ref={messagesEndRef} />
            </div>

            {/* Message Input */}
            <div className="border-t p-4">
              <form onSubmit={handleSubmit} className="flex space-x-2">
                <Input
                  type="text"
                  value={newMessage}
                  onChange={(e) => setNewMessage(e.target.value)}
                  placeholder="Type your message..."
                  className="flex-1"
                  disabled={isTyping}
                />
                <Button
                  type="submit"
                  disabled={!newMessage.trim() || isTyping}
                  className="bg-blue-600 hover:bg-blue-700 text-white px-6"
                >
                  Send
                </Button>
              </form>
            </div>
          </div>

          {/* Quick Actions */}
          <div className="mt-6 grid grid-cols-1 md:grid-cols-3 gap-4">
            <div className="bg-white p-4 rounded-lg shadow-md text-center">
              <h3 className="font-semibold mb-2">Book a Service</h3>
              <p className="text-sm text-gray-600 mb-3">Schedule your mobile mechanic appointment</p>
              <a
                href="/book"
                className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded text-sm transition-colors"
              >
                Book Now
              </a>
            </div>

            <div className="bg-white p-4 rounded-lg shadow-md text-center">
              <h3 className="font-semibold mb-2">Call Us</h3>
              <p className="text-sm text-gray-600 mb-3">Speak directly with our team</p>
              <a
                href="tel:8193199799"
                className="bg-red-600 hover:bg-red-700 text-white px-4 py-2 rounded text-sm transition-colors"
              >
                819 319 9799
              </a>
            </div>

            <div className="bg-white p-4 rounded-lg shadow-md text-center">
              <h3 className="font-semibold mb-2">Our Services</h3>
              <p className="text-sm text-gray-600 mb-3">View all available services</p>
              <a
                href="/services"
                className="bg-gray-600 hover:bg-gray-700 text-white px-4 py-2 rounded text-sm transition-colors"
              >
                View Services
              </a>
            </div>
          </div>
        </div>
      </main>
      
      <Footer />
    </div>
  )
}
